

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');


*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

body{
    width: 100vw;
    height: 100vh;
    font-family: "poppins", sans-serif;
    overflow-x: hidden;
}

.container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.title{
    font-size: 3rem;
    margin: 6vh;
    border: 7px dashed rgb(1, 17, 160);
    border-radius: 20px;
    padding: 1vh 5vw;
}

.cards{
    display: flex;
    justify-content: center;
    /* align-items: center; */
    flex-wrap: wrap;
    max-width: 1300px;
    margin: 0px auto;
}

.card{
    width: 400px;
    height: fit-content;
    padding: 1rem;
    margin: 1rem;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.image{
    width: 380px;
    aspect-ratio: 1/1;
    object-fit: cover;
}

.tour-info{
    margin: 20px 5px;
}

.tour-price{
    color: #1faa59;
    font-size: 1.3rem;
}

.tour-name{
    font-size: 1.5rem;
}

.read-more{
    color: #12b0e8;
    cursor: pointer;
}

.btn-red{
    margin-top: 18px;
    padding: 10px 80px;
    border: 1px solid #b4161b;
    cursor: pointer;
    border-radius: 10px;
    font-size: 18px;
    font-weight: bold;
    background-color: #b4161b21;
}

.btn-red:hover{
    background-color: red;
    color: white;
    transition: all 0.4s linear;
}

.refresh{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.refresh p{
    font-weight: bold;
    font-size: 1.5rem;
}

.btn-white{
    font-size: 1.2rem;
    margin-top: 18px;
    padding: 15px 40px;
    border: 1px solid white;
    cursor: pointer;
}

.btn-white:hover{
    background-color: #fff;
    transition: all 0.3s linear;

}